.graduating {
    width: 290px;
    border-left: none;
    padding: 10px 5px;
    margin: 40px 0;
    transform: translate(-260px);
    transition-duration: 500ms;
    background-color: darkslateblue;
}

.graduating:hover {
    transition-duration: 500ms;
    transform: translate(0);
}

.graduating > a {
    margin: 10px 0;
    padding: 0 15px;
    text-decoration: none;
    color: white;
}

.graduating > a:focus {
    color: darkslateblue;
    border: 1px solid black;
    border-radius: 10px;
    background-color: white;
    margin: 2px;
}

.graduating > a:hover {
    color: goldenrod;
}

.square::after {
    content: '';
    position: absolute;
    right: -45px;
    top: 7px;
    border: 15px solid transparent;
    border-left: 20px solid darkslateblue;
}