body {
    background-color: rgba(6, 141, 217, 0.55);
}

.title {
    display: flex;
    font-size: 20px;
    font-weight: bold;
    padding: 5px;
    width: 50%;
    border-radius: 45px;
    font-family: Tahoma;
    color: #491149;
    background: linear-gradient(21deg, rgba(255, 71, 66, 0.3), rgba(139, 0, 139, 0.3));
    justify-content: center;
    margin: 10px auto;
}

.graduating {
    width: fit-content;
    border: 2px solid gray;
    border-left: none;
    margin: 40px 0;
}

.graduating > span {
    margin: 15px;
    padding: 5px;
}