.container {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr;
    width: 100%;
}

.container > div {
    display: grid;
    height: 400px;
    align-items: flex-start;
    justify-content: center;
}

.container > div > a {
    display: grid;
    justify-content: center;
    font-size: 25px;
    font-family: Tahoma;
    font-style: italic;
}

.container > div > a:hover {
    color: darkslateblue;
    text-decoration: underline;
    cursor: pointer;
}